import React from "react";

function TableRow({ item, priceType }) {
    const getPrice = () => {
        if (priceType === " ") {
            return item.price;
        }
        if (priceType === " ") {
            return item.priceopt;
        }
        if (priceType === "  ( 250)") {
            return item.pricemegaopt;
        }
    };

    return (
        <tr>
            <td>{item.title}</td>
            <td>{item.number}</td>
            <td>{getPrice()}</td>
        </tr>
    );
}

export default TableRow;
